/* Полное покрытие экрана */
html, body {
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
    font-family: "Arial", sans-serif;
    background: #000; /* Чёрный фон */
    color: #fff;
    display: flex;
    flex-direction: column;
    text-align: center;
    overflow: hidden;
}

/* Главный контейнер */
.app {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
    width: 100%;
}

/* Верхняя панель с логотипом */
.header {
    width: 100%;
    background: rgba(20, 20, 30, 0.95);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0;
}

.logo {
    width: 150px;
}

/* Контент */
.content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    color: #fff;
}

/* Нижняя панель навигации */
.bottom-nav {
    width: 100%;
    background: rgba(0, 0, 0, 0.9);
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 15px 0;
    position: fixed;
    bottom: 0;
    border-radius: 20px 20px 0 0;
}

/* Кнопки меню */
.bottom-nav button {
    background: none;
    border: none;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    padding: 10px;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* Стили для иконок */
.button-icon {
    width: 32px;
    height: 32px;
    filter: brightness(0.7);
    transition: filter 0.3s ease-in-out, transform 0.2s ease-in-out;
}

/* Выделенная кнопка */
.bottom-nav button.active .button-icon {
    filter: brightness(1);
    transform: scale(1.2);
}

/* Эффект наведения */
.bottom-nav button:hover .button-icon {
    transform: scale(1.1);
}
